*{
  box-sizing: border-box;
}
//IMG
.imgs{
  margin-top: 4rem;
  margin-bottom: -50px;
  justify-content: space-between;
}

.content-img{
  width: 100%;
}

.link-btn{
  width: 100%;
  height: 50px;
  font-size: 20px;
}

.btn-more-work{
  padding: 10px;
   margin-left: 30rem;
   height: 4rem;
}

//LAYOUT

.main-art-details{
margin-left: 0;

.btn-more-work{
  margin: auto;
}
}
.container-img{
  grid-area: 1/1/-1/1;
  display: grid;
  //grid-template-columns: 1fr 2fr 2fr;
 // grid-template-rows: 0.5fr 3fr 2fr;
}
.main{
margin: 0 5rem 3rem;
}
.radio-buttons, .art-details{
margin-bottom: 1.25rem;
}
.details-modals{
  text-align: center;
}
.content-txt{
margin: 4rem 1.25rem 0 3.125rem;
text-align: start;
max-width: 50%;
p{
  margin-bottom: .8rem;
}
}
.artist-list-details{
margin: 3.125rem 0 0 1.25rem;
a{
  margin-bottom: 20px;
}
}
.main-art-details{
 align-items: none;
}

label{
  margin-left: .625rem;
}
.btn-more-work{
  margin-left: auto;
}
//RESPONSIVE

@media(max-width:1555px){
  .art-details{
    margin-top: 1.875rem;
  }
.main-art-details{
  text-align: center;
  flex-direction: column;
  align-items: center;
  .content-txt{
    max-width: none;
    align-items: center;
    }
  }
}
@media(max-width:$medium-breakpoint){
  .main-art-details{
    align-items: center;
  }
  .imgs{
    margin-bottom: 0.8rem;
    flex-direction: column;
    align-items: start;
    justify-content: space-around;
    .content-img{
      margin: 1rem 0 0 ;
      width: 50%;
     // height: 30%;
    }
    .content-txt{
      margin-left: 0px;
    }
  }
}
@media(max-width:$mobile-breakpoint){
  .content-txt{
    margin-left: 1rem;
  }
  .main{
    margin: 0px;
  }
  .imgs{
    margin: 0px;
  }
}

